@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#f9f9f9}.add-property-container{margin:0.5em 1em}.add-property-container>a{text-decoration:none}.add-property-form-container{position:absolute;margin-top:0.5em;padding:1em;background-color:white;border-bottom:2px solid #f9f9f9;z-index:1}.add-property-form-container>.cancel{position:absolute;right:1em}.add-property-form>label,.add-property-form input,.add-property-form select,.add-property-form button{font-size:1.25em;display:block}.add-property-form>label.checkbox,.add-property-form input[type=checkbox]{display:inline-block}.add-property-form>label{font-weight:bold;margin-top:0.5em}.add-property-form>label:first-of-type{margin-top:0}.add-property-form>input[type=text]{width:20em}.add-property-form>input[type=checkbox]{vertical-align:3px}.add-property-form>input[type=number]{width:5em}.add-property-form>button{margin-top:0.5em}.property-list-container{margin:0.5em 1em}.property-listing-container{display:grid;grid-template-columns:356px 1em 1fr;grid-template-rows:auto;grid-template-areas:"photo-container . details-container";margin-bottom:1em}.polaroid-container{background-color:white;grid-area:photo-container;display:grid;grid-template-columns:18px 320px 18px;grid-template-rows:18px 320px 18px 54px 18px;grid-template-areas:". . ." ". photo ." ". . ." ". caption ." ". . .";width:356px;box-shadow:0 0 10px lightgray}.polaroid-container>img{display:block;height:320px;width:320px;object-fit:cover;grid-area:photo}.polaroid-container>.caption{font-family:'Permanent Marker', cursive;font-size:1.5em;margin:0.25em 0 0 0;text-align:center;grid-area:caption}.details-container{grid-area:details-container}.cart-container{width:240px;position:absolute;top:0;right:0;background-color:rgba(255,255,255,0.95);padding-left:0.5em}.cart-indicator{text-align:right;padding-top:0.25em;height:1em}.cart-indicator .cart-icon{position:absolute;right:16px}.cart-indicator .cart-items-count{display:inline-block;width:16px;height:16px;background-color:red;color:white;border-radius:50%;text-align:center;font-size:12px;font-weight:bold;position:absolute;right:3px}.cart-items-list{padding:0 0.5em 0 0}.cart-total{text-align:right}.cart-hidden{background-color:transparent}.cart-hidden>.cart-items-list{display:none}.cart-item-delete{font-size:smaller;padding-right:0.5em}.stars-container{color:gray}.star-stacked{width:1.25em;height:1em;vertical-align:-0.125em}.empty-star{width:1.25em;display:inline-block}.filled-star{color:orange}.superhost{color:royalblue;padding-right:2px}@media screen and (max-width: 767px){.property-listing-container{display:grid;grid-template-columns:auto;grid-template-rows:auto;grid-template-areas:"photo-container" "details-container";margin-bottom:1em}}.fake-link{color:blue}.close-box,.cart-icon,.cart-items-count,.fake-link{cursor:pointer}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

